@import url("https://fonts.googleapis.com/css2?family=Lalezar&display=swap");

:root {
	/** TEXT **/
	--main-font: "Lalezar", cursive;
	--base-font-size: 18px;
	--text-color: #fff;
	--heading-color: var(--main-dark);

	/** COLORS **/
	--main-dark: #10192b;
	--main-light: #ca5b90;
	--main-accent: #5c90b5;
}
